@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@200;300;400;600;700;900&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

body {
  font-family: "Source Sans Pro", sans-serif;
}

.header__container {
  display: flex;
  justify-content: space-between;
}

.header__container__right {
  display: flex;
}

.searchBar__text {
  width: 160px;
  height: 30px;
  margin-top: 0.5rem;
  border-radius: 5px;
  border: 1px solid #fea500;
  text-align: center;
}
.searchBar__text-deactive {
  display: none;
}

.shoppingCart__modal {
  width: 85%;
  height: 200px;
  background-color: #444;
  margin: 2rem auto;
  text-align: center;
  padding-top: 4rem;
  color: #fff;
}

.shoppingCart__modal-deactive {
  display: none;
}

.hamburgerMenu__icon {
  font-size: 22px;
  margin-top: 1rem;
  margin-left: 1rem;
}

.hamburger__menu__content {
  display: flex;
  flex-direction: column;
  background-color: grey;
  height: 100vh;
  width: 80%;
  position: absolute;
}
.hamburger__menu__content__deactive {
  display: none;
}
.hamburger__menu__content a {
  color: #fff;
  text-decoration: none;
  padding-top: 1.5rem;
  text-align: center;
  font-size: 14px;
}
.hamburger__menu__content hr {
  margin-top: 0.5rem;
}

.windowClose__icon {
  margin-left: auto;
  font-size: 24px;
  cursor: pointer;
}

.searchBar__icon,
.user__icon,
.shoppingCart__icon {
  margin-right: 20px;
  font-size: 18px;
  cursor: pointer;
  margin-top: 1rem;
  margin-left: 1rem;
}

.shoppingCart__container {
  position: relative;
}

.shoppingCart__item {
  background-color: #fea500;
  width: 25px;
  height: 25px;
  text-align: center;
  border-radius: 50%;
  font-weight: bolder;
  position: absolute;
  padding-top: 0.1rem;
  top: 0;
  left: 30px;
  bottom: 0;
  right: 0;
}

.logo img {
  display: block;
  max-width: 15%;
  margin: 1rem auto 0;
  cursor: pointer;
}

@media only screen and (min-width: 892px) {
  .logo img {
    max-width: 5%;
  }
}

.banner__container {
  display: grid;
  grid-template-columns: 1fr;
}

.banner__container img {
  max-width: 100%;
  height: auto;
  cursor: pointer;
  margin: 1rem auto 0;
}

.banner__container img:hover {
  opacity: 0.6;
}

@media only screen and (min-width: 892px) {
  .banner__container {
    grid-template-columns: 1fr 1fr;
  }
  .banner__container {
    padding: 0 5rem;
  }
}

.sales__container {
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.sales__container img {
  max-width: 100%;
  height: auto;
}

.sales__container a {
  text-decoration: none;
  color: #000000;
  text-align: center;
}

.best__seller__price {
  color: #941313;
  font-weight: bold;
  text-align: center;
}

@media only screen and (min-width: 892px) {
  .sales__container {
    grid-template-columns: 1fr 1fr 1fr;
  }
}

@media only screen and (min-width: 1200px) {
  .sales__container {
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
  }
}

.footer__lists {
  display: flex;
  justify-content: space-around;
}

.footer__lists h3 {
  font-weight: lighter;
}

.footer__lists a {
  text-decoration: none;
  color: #ccc;
  font-size: 12px;
}

.footer__lists a:hover {
  color: #fea500;
}

@media only screen and (max-width: 700px) {
  .footer__container {
    background: #222222;
    color: #ffffff;
    margin-top: 4rem;
  }
  .footer__lists {
    display: flex;
    flex-direction: column;
  }
  .company__info,
  .customer__service,
  .my__account,
  .contact__us,
  .footer__copyright {
    margin: 2rem 0 0 2rem;
  }
  .footer__lists hr {
    width: 95%;
    margin-top: 0.5rem;
    background: #666;
  }

  .footer__lists h3 {
    font-size: 16px;
  }
  .footer__lists ul {
    margin-top: 1rem;
  }

  .footer__lists a {
    font-size: 14px;
  }
  .gmail__hr {
    margin-top: 4rem;
    padding: 1px;
    background: #666;
  }

  .footer__copyright__left {
    font-size: 15px;
  }
  .footer__copyright__left span {
    color: #fea500;
    font-weight: bold;
  }

  .footer__copyright__right img {
    max-width: 100%;
    height: auto;
    margin: 3rem 1rem 0.5rem 3rem;
  }

  .company__info li,
  .customer__service li,
  .my__account li,
  .contact__us li {
    margin-top: 1rem;
    margin-left: 2rem;
  }
}

.shirtSales__container {
  margin-top: 2rem;
}

.mainShirt__front {
  max-width: 100%;
  height: auto;
  margin-top: 2rem;
}

.mainShirt__small {
  margin-left: 1rem;
}

.mainShirt__small img {
  margin: 2rem 0.5rem;
  border: 1px solid #808080;
  cursor: pointer;
}

.shirtSales__container h1 {
  color: #444444;
  font-weight: bold;
  font-size: 120.57%;
  margin: 15px 0 10px 15px;
}

.shirtSales__price__container {
  display: flex;
  margin-top: 1rem;
  margin-left: 1rem;
}

.shirtSales__price {
  font-size: 2em;
  color: #941212;
  font-weight: normal;
  line-height: 28px;
}

.price__hr {
  margin-top: 1rem;
}

.in__stock {
  margin-left: 2rem;
  border: 1px solid #ddd;
  width: 70px;
  height: 30px;
  text-align: center;
  line-height: 28px;
}

.user__star {
  color: #fea500;
  margin-top: 1rem;
  margin-left: 1rem;
}
.user__star__hr {
  margin-top: 1rem;
}

.size__chart {
  display: flex;
  grid-gap: 2rem;
  gap: 2rem;
  margin-left: 1rem;
  margin-top: 1rem;
}

.size__chart a:first-child {
  color: #ff0000;
  font-size: 1.1em;
  text-decoration: underline;
}

.size__chart a:last-child {
  color: #000;
  font-size: 1.1em;
}

.size__chart__hr {
  margin-top: 1rem;
}

.shipping__information {
  margin-top: 1rem;
  padding: 20px;
}

.tshirtSales__additional__parts {
  background-color: #f5f5f5;
  width: 95%;
  height: 300px;
  margin: 3rem auto 0;
  color: #777777;
}
.tshirt__size,
.tshirt__name,
.tshirt__number {
  display: flex;
  flex-direction: column;
  margin: 2rem auto;
}

.tshirt__size select,
.tshirt__name input,
.tshirt__number input {
  width: 240px;
  height: 30px;
  margin-top: 0.4rem;
  text-align: center;
}

.tshirt__size span {
  color: #ff0000;
}

.tshirt__name span,
.tshirt__number span {
  color: #000;
  font-weight: bold;
}

.tshirtSales__additional__parts p {
  color: #ff0000;
  margin-top: 2rem;
}

.heart__icon {
  display: flex;
}

.add__to__wishlist {
  width: 140px;
  height: 60px;
  border: 1px solid #444;
  text-align: center;
  padding-top: 0.3rem;
  margin: 2rem 2.5rem;
  line-height: 28px;
}

.add__to__wishlist-color {
  color: #808080;
}

.add__to__wishlist-red {
  color: #ff0000;
  -webkit-animation: changeColor 1s;
          animation: changeColor 1s;
}

@-webkit-keyframes changeColor {
  from {
    -webkit-transform: scale(2);
            transform: scale(2);
  }
  to {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes changeColor {
  from {
    -webkit-transform: scale(2);
            transform: scale(2);
  }
  to {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

.shirtSales__container button {
  display: block;
  width: 140px;
  height: 60px;
  background-color: #fea500;
  color: #fff;
  font-weight: bold;
  font-size: 16px;
  text-align: center;
  margin: 2rem 1rem;
}

.thirt__description {
  width: 95%;
  margin: 2rem auto;
  border: 1px solid #e9e9e9;
}

.thirt__description h1 {
  color: #fea500;
  font-weight: 700;
}

.tshirt__list {
  margin-top: 1rem;
  padding-left: 2rem;
  line-height: 28px;
}

.tshirt__description__text {
  margin-top: 2rem;
  padding: 0 2rem;
  font-weight: bold;
}

.tshirt__description__text span {
  color: #ff0000;
}

.thirt__description img {
  max-width: 100%;
  height: auto;
  margin-top: 2rem;
}

.about__us__container h1,
.delivery__container h1,
.privacy__container h1,
.terms__container h1,
.faqHelp__container h1,
.ship__container h1,
.returnPolicy__container h1,
.payment__container h1,
.create__account__container h1,
.logIn__container h1,
.forget__password__container h1 {
  font-size: 24px;
  font-weight: bold;
  color: #444444;
  text-align: center;
  margin-top: 2rem;
}

.about__us__text,
.delivery__text,
.privacy__text,
.terms__text,
.faqHelp__text,
.shipping__text,
.returnPolicy__text,
.payment__text,
.forget__password__text {
  border: 1px dotted #444444;
  margin-top: 1rem;
  margin-left: 0.5rem;
  margin-right: 0.5rem;
}

.about__us__text p,
.delivery__text p,
.privacy__text p,
.terms__text p,
.faqHelp__text p,
.shipping__text p,
.returnPolicy__text p,
.payment__text p,
.forget__password__text p {
  padding: 20px;
  font-size: 16px;
}

.faqHelp__text p,
.shipping__text p,
.returnPolicy__text p,
.payment__text p {
  line-height: 25px;
}

.faqHelp__text h4,
.shipping__text h4,
.returnPolicy__text h4 {
  text-align: center;
  margin-top: 2rem;
}

.payment__text img {
  max-width: 100%;
  height: auto;
}

.create__account__form {
  display: flex;
  flex-direction: column;
  margin: 1rem;
}

.create__account__form input {
  width: 85%;
  height: 30px;
}

.create__account__form label {
  color: #444;
  margin-top: 1rem;
}

.create__account__form button {
  width: 180px;
  height: 60px;
  margin: 2rem auto 0;
  background-color: #fea500;
  color: #fff;
  font-weight: bold;
}

.create__account__container {
  position: relative;
}

.sign__up__modal {
  color: green;
  padding-top: 0.2rem;
}

.sign__up__modal-error {
  color: red;
  padding-top: 0.2rem;
}

.registration__container,
.already__registered {
  border: 1px solid #444;
  width: 85%;
  margin: 0 auto;
  height: 240px;
  margin-top: 1.5rem;
}
.already__registered {
  height: 280px;
}

.registration__container h4,
.already__registered h4 {
  color: #444;
  font-size: 24px;
  margin-top: 0.5rem;
  margin-left: 0.7rem;
}

.registration__container p,
.already__registered p {
  margin-top: 1.5rem;
  margin-left: 0.7rem;
}

.createAccount__button,
.logIn__button {
  width: 140px;
  height: 45px;
  margin-top: 1rem;
  float: right;
  margin-right: 0.5rem;
  border-radius: 5px;
  background-color: #fea500;
  color: #fff;
  font-weight: bold;
}

.already__registered {
  margin-top: 6rem;
}

.forgot__password {
  padding-top: 0.8rem;
  text-decoration: none;
  color: #000;
}

.size__chart__container h1 {
  font-size: 24px;
  font-weight: bold;
  color: #444444;
  text-align: center;
  margin-top: 2rem;
}

.men__jersey,
.women__jersey {
  border: 1px solid black;
}

.sales__container {
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.sales__container img {
  max-width: 100%;
  height: auto;
  margin-top: 4rem;
}

.tshirt__names {
  padding: 1rem;
}

.best__seller__price {
  color: #941313;
  font-weight: bold;
  text-align: center;
}

.show__more__button {
  width: 140px;
  height: 60px;
  margin: 2rem -70px 0;
  position: relative;
  top: 50%;
  left: 50%;
  background-color: #222;
  color: #fff;
  font-weight: bolder;
  text-align: center;
}

.show__more__button-deactive {
  display: none;
}

@media only screen and (min-width: 892px) {
  .sales__container {
    grid-template-columns: 1fr 1fr 1fr;
  }
}

@media only screen and (min-width: 1200px) {
  .sales__container {
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
  }
}

